.App {
    text-align: center;
}

body {
    background-image: linear-gradient(95.2deg, rgba(173, 252, 234, 1) 26.8%, rgba(192, 229, 246, 1) 64%);
    height: 80vh;
}

#calc {
    border: 1px solid darkgray;
    border-radius: 40px;
    margin: 10vh auto;
    max-width: 350px;
    min-width: 300px;
    padding: 30px;
    background: rgb(69, 68, 68);
    background: linear-gradient(0deg, rgba(69, 68, 68, 1) 1%, rgba(23, 27, 33, 1) 4%, rgba(0, 0, 0, 1) 86%, rgba(97, 97, 97, 1) 100%);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

#display-container {
    font-family: 'Saira Condensed', sans-serif;
    text-align: right;
    height: 100px;
    border: 4px solid black;
    border-radius: 10px;
    background: rgb(227, 249, 237);
    background: linear-gradient(0deg, rgba(227, 249, 237, 1) 0%, rgba(189, 251, 232, 1) 100%);
}

#formula {
    height: 20px;
    font-size: 1rem;
    color: #2c2929;
    padding: 4px 10px 4px 10px;
}

#display {
    font-size: 2.2rem;
    height: 30px;
    padding: 0 12px 20px 5px;
}

#device-data {
    font-family: monospace;
    height: 35px;
    background-color: black;
    text-align: left;
    color: #6b6b6b;
    font-size: 0.7rem;
    font-weight: bold;
    padding-top: 6px;
}

.calc-button {
    background-color: white;
    margin: 0;
    padding: 0;
    min-height: 70px;
}
.operator button {
    background-color: pink !important;
}

.big button {
    background-color: lightblue !important;
}

.calc-button button {
    font-size: 1.8rem;
    border: 0;
    border-radius: 20px;
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;

}

.calc-button button:hover {
    background-color: #a7dac3 !important;
    cursor: pointer;
}



/*Grid*/
#calc-grid {
    display: grid;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 3px;
}
#calc-grid div {
    background: 0;
    border: 0;
}
#enter-button {
    grid-row-start: 4;
    grid-column-start: 4;
    grid-row-end: 6;
    grid-column-end: 5;
}

#ac-button {
    grid-row-start: 1;
    grid-column-start: 1;
    grid-row-end: 1;
    grid-column-end: 3;
}

#zero-button {
    grid-row-start: 5;
    grid-column-start: 1;
    grid-row-end: 5;
    grid-column-end: 3;
}